* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

ul {
  list-style: none; }

a,
a:hover {
  text-decoration: none; }

input,
button,
textarea,
select {
  outline: none !important; }

button {
  cursor: pointer; }

:root {
  font-size: 1rem; }
  @media (max-width: 767.98px) {
    :root {
      font-size: 0.9rem; } }
  @media (max-width: 575.98px) {
    :root {
      font-size: 0.8rem; } }

body {
  font-family: 'Rubik', sans-serif;
  font-weight: 400;
  background-color: #fff;
  font-size: 1rem;
  text-align: center; }

.fixed-top {
  position: fixed !important;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 99;
  background-color: #000;
  background-image: url("../img/header_bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  justify-content: center !important;
  align-items: center !important; }
  @media (max-width: 575.98px) {
    .fixed-top {
      padding: 1.5rem 2.675rem !important; } }

.menu-open {
  display: flex !important;
  flex-direction: column;
  position: absolute; }

.menu-open__social-lang {
  width: 100%;
  flex-direction: row !important;
  justify-content: space-between !important;
  padding: 0 1rem; }
  @media (max-width: 575.98px) {
    .menu-open__social-lang {
      padding: 0 1.875rem; } }

@media (max-width: 991.98px) {
  .burger-logo {
    top: 5.6%; } }

@media (max-width: 767.98px) {
  .burger-logo {
    top: 5.2%; } }

@media (max-width: 575.98px) {
  .burger-logo {
    top: 4.7%; } }

.header-top {
  padding-top: 4rem; }

.header {
  background-color: #000;
  background-image: url("../img/header_bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  color: #fff;
  height: 100vh; }
  @media (max-width: 767.98px) {
    .header {
      height: 37.25rem; } }
  @media (max-width: 575.98px) {
    .header {
      height: calc(100vh - 5vh); } }
  @media (max-width: 575.98px) {
    .header {
      padding-left: 0.8rem;
      padding-right: 0.8rem; } }
  .header__title {
    margin: 4rem 0 2rem 0;
    font-size: 3rem; }
  .header__subtitle {
    margin: 0 auto 3.2rem;
    font-size: 1.3rem;
    line-height: 1.5;
    font-weight: 400;
    max-width: 31.25rem; }
  .header__linktotg {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 16.25rem;
    height: 3.75rem;
    background-color: #ffc000;
    text-transform: uppercase;
    color: #fff;
    font-weight: 600;
    border-radius: 0.3rem;
    margin-bottom: 2.5rem;
    transition: 0.6s ease;
    border: 2px solid transparent; }
    .header__linktotg:hover {
      border-color: #ffc000;
      background-color: #fff;
      color: #ffc000; }
  .header__arrow-anchor {
    display: block;
    width: 2.5rem;
    margin: 0 auto;
    animation: moveUpDown 1.2s ease-in-out infinite alternate; }

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 1.875rem;
  position: relative; }
  .nav__list {
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 120; }
    @media (max-width: 991.98px) {
      .nav__list {
        display: none; } }
    .nav__list__item:nth-child(2) {
      padding: 0 1.3rem; }
      @media (max-width: 991.98px) {
        .nav__list__item:nth-child(2) {
          padding: 0; } }
    @media (max-width: 991.98px) {
      .nav__list__item {
        padding: 0 0 1rem; } }
    .nav__list__item__link {
      color: #fff;
      font-size: 1rem;
      font-weight: 600; }
  .nav__logo {
    color: #fff;
    font-weight: 700;
    font-size: 1.7rem;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%); }
  .nav__social-lang {
    display: flex;
    justify-content: center;
    align-items: center; }
    @media (max-width: 991.98px) {
      .nav__social-lang {
        display: none; } }
    .nav__social-lang__social-list {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 1.875rem; }
      @media (max-width: 991.98px) {
        .nav__social-lang__social-list {
          margin-right: 0;
          flex-direction: column; } }
      .nav__social-lang__social-list__item {
        padding: 0 0.5rem; }
        @media (max-width: 991.98px) {
          .nav__social-lang__social-list__item {
            padding: 0.3rem 1rem; } }
        .nav__social-lang__social-list__item__link {
          display: inline-flex;
          align-items: center;
          justify-content: center;
          color: #000;
          font-size: 1rem;
          background-color: #fff;
          width: 1.875rem;
          height: 1.875rem;
          border-radius: 50%;
          border: 1px solid transparent;
          transition: 0.6s ease; }
          .nav__social-lang__social-list__item__link:hover {
            background-color: #000;
            border-color: #fff;
            color: #fff; }
    .nav__social-lang__lang-list {
      display: flex;
      justify-content: center;
      align-items: center; }
      @media (max-width: 991.98px) {
        .nav__social-lang__lang-list {
          flex-direction: column; } }
      .nav__social-lang__lang-list__item {
        padding: 0 0.6rem; }
        @media (max-width: 991.98px) {
          .nav__social-lang__lang-list__item {
            padding: 0.3rem 1rem; } }
        .nav__social-lang__lang-list__item__link {
          color: rgba(255, 255, 255, 0.6);
          font-size: 1rem;
          font-weight: 600; }
          .nav__social-lang__lang-list__item__link--active {
            color: #fff; }
          .nav__social-lang__lang-list__item__link:disabled {
            color: rgba(255, 255, 255, 0.6); }
  .nav__burger {
    display: none;
    position: relative;
    z-index: 4;
    width: 26px;
    height: 12px;
    transform: rotate(0);
    transition: 0.3s;
    border: none;
    background: none;
    margin-left: auto;
    align-self: flex-start; }
    @media (max-width: 991.98px) {
      .nav__burger {
        display: inline-block; } }
    .nav__burger span {
      width: 100%;
      height: 2px;
      background: #fff;
      border-radius: 10px;
      position: absolute;
      left: 0;
      transform: rotate(0);
      transition: 0.3s; }
      .nav__burger span:nth-child(1) {
        top: 0; }
      .nav__burger span:nth-child(2), .nav__burger span:nth-child(3) {
        top: 6px; }
      .nav__burger span:nth-child(4) {
        top: 12px; }
    .nav__burger.open span:nth-child(1), .nav__burger.open span:nth-child(4) {
      width: 0;
      left: 50%;
      top: 6px; }
    .nav__burger.open span:nth-child(2) {
      transform: rotate(45deg); }
    .nav__burger.open span:nth-child(3) {
      transform: rotate(-45deg); }

.about {
  padding: 5.125rem 0; }
  @media (max-width: 575.98px) {
    .about {
      padding-left: 0.8rem;
      padding-right: 0.8rem; } }
  .about__title {
    font-size: 1.7rem;
    margin-bottom: 1.4rem; }
  .about__text {
    font-size: 1.2rem;
    max-width: 44rem;
    margin: 0 auto; }
    .about__text__link {
      color: #ffbb00;
      border-bottom: 3px solid #ffbb00; }

@keyframes moveUpDown {
  0% {
    transform: translateY(0); }
  100% {
    transform: translateY(1rem); } }
